import { useParams } from 'react-router-dom';
import collageapi from 'api/collage';
import helpers from 'components/collage/helpers';
import BaseCollage from 'pages/collage/Base';

async function downloadImages(imgURLS) {
  const urlFileMap = {};
  console.time('downloading images');
  await Promise.allSettled(
    Object.entries(imgURLS).map(([title, url]) =>
      fetch(url)
        .then((resp) => resp.blob())
        .then((f) => {
          urlFileMap[title] = f;
        })
    )
  );

  return urlFileMap;
}

async function convertToBase64(imgFiles) {
  const urlBase64Map = {};
  console.time('base64 images');
  await Promise.allSettled(
    Object.entries(imgFiles).map(([title, f]) =>
      helpers.convertBase64(f).then((b64) => {
        urlBase64Map[title] = b64;
      })
    )
  );
  return urlBase64Map;
}

async function getImages(imgDets, urls, handleImageDetails, startLoad) {
  const urlFileMap = await downloadImages(urls);
  const urlBase64Map = await convertToBase64(urlFileMap);
  startLoad();
  const tempImages = await Promise.allSettled(
    imgDets.map((img) => {
      const result = new Promise((resolve) => {
        const { naturalWidth, naturalHeight, title, aspectRatio, type } = img;
        const imageSRC = urlFileMap[title];
        const base64SRC = urlBase64Map[title];
        const image = new Image();
        image.onload = () => {
          const imageDetail = {
            title,
            src: helpers.optimizeImage(image, 1500, 1500 / aspectRatio),
            originalSRC: base64SRC,
            originalHeight: naturalHeight,
            originalWidth: naturalWidth,
            naturalHeight: 1500 / aspectRatio,
            naturalWidth: 1500,
            aspectRatio,
            type,
            file: imageSRC,
          };
          handleImageDetails(imageDetail);
          resolve(imageDetail);
        };
        image.src = base64SRC;
      });
      return result;
    })
  );
  return tempImages.map((elem) => elem.value);
}

export default function CollageView() {
  const params = useParams();
  const { collageID } = params;

  const getCollage = async (
    handleImageCount,
    handleNewImageCount,
    handleImageDetails
  ) => {
    const details = await collageapi.getCollage(collageID);
    const images = await getImages(
      details.details.images,
      details.urls,
      handleImageDetails,
      () => {
        handleImageCount(details.details.images.length);
        handleNewImageCount(details.details.images.length);
      }
    );

    const imageDets = images.map((img) => {
      const { src, originalSRC, file, ...rest } = img;
      return rest;
    });
    return { details: { ...details.details, images: imageDets }, images };
  };

  return (
    <BaseCollage
      getCollage={getCollage}
      clearCollage={() => collageapi.deleteCollage(collageID)}
      saveType="existing"
    />
  );
}
